import * as React from 'react';
import { navigate } from 'gatsby';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Container from '@mui/material/Container';
import IconButton from '@mui/material/IconButton';
import Link from '@mui/material/Link';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemText from '@mui/material/ListItemText';
import SwipeableDrawer from '@mui/material/SwipeableDrawer';
import Typography from '@mui/material/Typography';
import MenuIcon from '@mui/icons-material/Menu';
import { scrollToTop, scrollToSection } from '../utils/scroll';

export const Menu = ({ lists, isTop = false }) => {
  const [isOpen, setIsOpen] = React.useState(false);

  const toggleDrawer = _isOpen => event => {
    if (
      event &&
      event.type === 'keydown' &&
      (event.key === 'Tab' || event.key === 'Shift')
    ) {
      return;
    }

    setIsOpen(_isOpen);
  };

  return (
    <AppBar position='fixed' color='inherit'>
      <Container>
        <Box
          sx={{
            display: 'flex',
            justifyContent: 'space-between',
            alignItems: 'center',
            height: '68px'
          }}
        >
          <Box>
            <Link
              href='#top'
              sx={{
                display: 'block'
              }}
              underline='none'
              onClick={e => {
                e.preventDefault();

                if (isTop) {
                  scrollToTop();
                } else {
                  navigate('/');
                }
              }}
            >
              <Typography sx={{ fontSize: 24 }}>yu-hi</Typography>
            </Link>
          </Box>
          <Box>
            {lists.map(({ link, label }, i) => {
              const isContact = link === '#contact';
              const isFirst = i === 0;
              const isLast = i === lists.length - 1;

              return (
                <Button
                  key={i}
                  href={link}
                  variant={isContact ? 'outlined' : 'text'}
                  sx={{
                    marginLeft: isFirst ? 0 : '1em',
                    marginRight: isLast ? 0 : '1em',
                    borderRadius: 0,
                    letterSpacing: '0.1em',
                    display: {
                      xs: 'none',
                      md: 'inline'
                    }
                  }}
                  onClick={e => {
                    e.preventDefault();
                    scrollToSection(link.replace('#', ''));

                    if (/^#/.test(link)) {
                      scrollToSection(link.replace('#', ''));
                    } else {
                      navigate(link);
                    }
                  }}
                >
                  {label}
                </Button>
              );
            })}
            <IconButton
              onClick={toggleDrawer(true)}
              sx={{
                display: {
                  xs: 'block',
                  md: 'none'
                }
              }}
            >
              <MenuIcon />
            </IconButton>
          </Box>
        </Box>
      </Container>

      <SwipeableDrawer
        anchor='right'
        open={isOpen}
        onClose={toggleDrawer(false)}
        onOpen={toggleDrawer(true)}
      >
        <Box
          sx={{
            width: 200,
            height: '100%'
          }}
          role='presentation'
          onClick={toggleDrawer(false)}
          onKeyDown={toggleDrawer(false)}
        >
          <List>
            {lists.map(({ link, label }, i) => (
              <ListItem
                button
                key={i}
                onClick={e => {
                  e.preventDefault();

                  if (/^#/.test(link)) {
                    scrollToSection(link.replace('#', ''));
                  } else {
                    navigate(link);
                  }
                }}
              >
                <ListItemText
                  primary={<Typography color='primary'>{label}</Typography>}
                />
              </ListItem>
            ))}
          </List>
        </Box>
      </SwipeableDrawer>
    </AppBar>
  );
};
