import * as React from 'react';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';

export const SectionTitle = ({
  children,
  variant = 'h2',
  align = 'left',
  sub
}) => (
  <Box mb={variant === 'h2' ? 8 : 4}>
    <Typography
      variant={variant}
      align={align}
      sx={{
        letterSpacing: 4
      }}
    >
      {children}
    </Typography>
    {sub && (
      <Typography
        align={align}
        sx={{
          letterSpacing: 2
        }}
      >
        {sub}
      </Typography>
    )}
  </Box>
);
