import * as React from 'react';
import { StaticImage } from 'gatsby-plugin-image';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Container from '@mui/material/Container';
import Grid from '@mui/material/Grid';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableRow from '@mui/material/TableRow';
import TextField from '@mui/material/TextField';
import Typography from '@mui/material/Typography';
import { Layout } from '../components/layout';
import { Seo } from '../components/seo';
import { Section } from '../components/section';
import { SectionTitle } from '../components/section-title';
import { Menu } from '../components/menu';
import { Footer } from '../components/footer';

import background from '../images/background.webp';
import b2 from '../images/b2.webp';
import img1 from '../images/01.jpg';
import img2 from '../images/02.jpg';
import img3 from '../images/03.jpg';

const IndexPage = () => {
  const [openingAnimation, setOpeningAnimation] = React.useState(0);
  const [isShow, setIsShow] = React.useState(false);

  React.useEffect(() => {
    // @TODO Opening animation
    const { searchParams } = new URL(window.location.href);
    const openingAnimation = Number(searchParams.get('openingAnimation'));

    if (!!openingAnimation) {
      setTimeout(() => {
        setIsShow(true);
      }, 500);
    } else {
      setIsShow(true);
    }

    setOpeningAnimation(openingAnimation);
  }, []);

  const openingAnimationStyle = {
    position: 'fixed',
    left: 0,
    width: '100%',
    height: '50vh',
    boxSizing: 'border-box',
    backgroundColor: '#fff',
    transitionProperty: 'transform,opacity',
    transitionDuration: '500ms',
    transitionTimingFunction: 'ease-out',
    transform: `scaleY(${isShow ? 0 : 1})`,
    opacity: isShow ? 0.5 : 1,
    zIndex: 2000
  };

  return (
    <Layout>
      {openingAnimation === 1 && (
        <>
          <div
            style={{
              ...openingAnimationStyle,
              top: 0,
              borderBottom: '1px solid #000',
              transformOrigin: 'top'
            }}
          />
          <div
            style={{
              ...openingAnimationStyle,
              bottom: 0,
              borderTop: '1px solid #000',
              transformOrigin: 'bottom'
            }}
          />
        </>
      )}

      <Menu
        isTop={true}
        lists={[
          {
            link: '#about',
            label: 'ABOUT'
          },
          {
            link: '#shopping',
            label: 'SHOPPING'
          },
          {
            link: '#company',
            label: 'ORGANIZATION'
          }
        ]}
      />

      {/* MainVisual */}
      <Box
        component='section'
        sx={{
          position: 'relative',
          width: '100%',
          height: '100vh',
          overflow: 'hidden'
        }}
      >
        <img
          width={3840}
          height={2160}
          src={background}
          style={{
            display: 'block',
            width: '100%',
            height: '100%',
            objectFit: 'cover',
            objectPosition: '25% 50%'
          }}
        />
        <Box
          sx={{
            position: 'absolute',
            top: 0,
            right: 0,
            bottom: 0,
            left: 0,
            width: '100%',
            height: '100%',
            backgroundColor: 'rgba(255, 255, 255, 0.1)'
          }}
        >
          <Container>
            <Typography
              variant='h1'
              sx={{
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'end',
                height: '100vh',
                fontWeight: 'lighter',
                color: 'primary.light',
                pointerEvents: 'none',
                letterSpacing: '0.1em',
                fontSize: {
                  md: 48,
                  sm: 32,
                  xs: 24
                },
                lineHeight: 1.5
              }}
            >
              こだわりを持って
              <br />
              アクセサリーを選ぶ
              <br />
              あなたに、
            </Typography>
          </Container>
        </Box>
      </Box>

      <Section
        id='about'
        theme='light'
        sx={{
          backgroundImage: `url(${b2})`,
          backgroundSize: 'cover',
          backgroundRepeat: 'no-repeat',
          backgroundPosition: '65% 0%'
        }}
      >
        <SectionTitle align='center' sub='私たちについて'>
          ABOUT
        </SectionTitle>
        <Grid container>
          <Grid item md={6} xs={12}>
            <Typography variant='h3' my={6}>
              古き良き都、京都
            </Typography>
            <Typography variant='h3' my={6}>
              芸術家の家系に生まれた
            </Typography>
            <Typography variant='h3' my={6}>
              2人の兄弟が彩る
            </Typography>
            <Typography variant='h3' my={6}>
              唯一無二の作品。
            </Typography>
          </Grid>
        </Grid>
      </Section>

      <Section id='shopping'>
        <SectionTitle align='center' sub='ショッピング'>
          SHOPPING
        </SectionTitle>
        <Grid container spacing={8}>
          {[
            {
              img: (
                <StaticImage src='../images/01.jpg' width={250} height={250} />
              )
            },
            {
              img: (
                <StaticImage src='../images/02.jpg' width={250} height={250} />
              )
            },
            {
              img: (
                <StaticImage src='../images/03.jpg' width={250} height={250} />
              )
            }
          ].map(({ img }, i) => (
            <Grid key={i} item sm={4} xs={12}>
              <Box
                display='flex'
                justifyContent='center'
                alignItems='center'
                flexDirection='column'
              >
                <Box>{img}</Box>
                <Box>
                  <Typography mt={1}>イヤリング</Typography>
                  <Typography>XX,XXX円</Typography>
                </Box>
              </Box>
            </Grid>
          ))}
        </Grid>
      </Section>

      <Section id='company' theme='dark'>
        <SectionTitle align='center' sub='運営団体'>
          ORGANIZATION
        </SectionTitle>
        <Grid container justifyContent='center'>
          <Grid item md={8} xs={12}>
            <TableContainer>
              <Table>
                <TableBody>
                  {[
                    {
                      key: '屋号',
                      value: (
                        <>
                          ユーヒ京都
                          <br />
                          <small>(yu-hi kyoto)</small>
                        </>
                      )
                    },
                    {
                      key: '代表者名',
                      value: <>Yamazaki</>
                    },
                    {
                      key: '主な事業内容',
                      value: <>アクセサリーの制作・販売</>
                    },
                    {
                      key: '所在地',
                      value: (
                        <>
                          京都府京都市
                          <br />
                          台湾台北市XX区
                        </>
                      )
                    },
                    {
                      key: '設立年月日',
                      value: '2023年XX月XX日'
                    }
                  ].map(({ key, value }, i) => (
                    <TableRow
                      key={i}
                      sx={{
                        '&:last-child td, &:last-child th': { border: 0 }
                      }}
                    >
                      <TableCell component='th' scope='row'>
                        <Typography color='primary.dark' fontWeight='bold'>
                          {key}
                        </Typography>
                      </TableCell>
                      <TableCell>
                        <Typography color='primary.dark'>{value}</Typography>
                      </TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            </TableContainer>
          </Grid>
        </Grid>
      </Section>

      <Section id='contact'>
        <SectionTitle align='center' sub='お問い合わせ'>
          CONTACT
        </SectionTitle>
        <Grid container justifyContent='center'>
          <Grid item md={6} xs={12}>
            <Box>
              {[
                {
                  label: '氏名'
                },
                {
                  label: 'メールアドレス'
                },
                {
                  label: 'お問い合わせ内容',
                  multiline: true,
                  rows: 4
                }
              ].map(({ label, multiline = false, rows = 1 }, i) => (
                <TextField
                  key={i}
                  variant='outlined'
                  label={label}
                  multiline={multiline}
                  rows={rows}
                  fullWidth
                  sx={{
                    my: 2,
                    '& fieldset': {
                      borderRadius: '0px'
                    }
                  }}
                />
              ))}
              <Button
                variant='outlined'
                size='large'
                sx={{
                  width: '100%',
                  borderRadius: 0
                }}
              >
                送信
              </Button>
            </Box>
          </Grid>
        </Grid>
      </Section>

      <Footer />
    </Layout>
  );
};

export default IndexPage;

export const Head = () => <Seo />;
